*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

.plywood-Component6-div{
    width: 100%;
    height: 50vh;
    background-color: white;  
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.plywood-Component6-heading-img{
    
    width: 312px;
    height: 70px;
    display: flex;
    flex-direction: row;
    align-items:center;
    /* justify-content: center; */
    background-image: url('../assets/nagaland.jpg');
    background-repeat: no-repeat;   

}

.plywood-Component6-heading-content{
    color: white;
    text-shadow: 0px 39px 25px rgba(0, 0, 0, 0.25);
    font-family: Cormorant Garamond;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 1px;
    text-transform: uppercase;
    position: relative;
    top: 15px;
    right: -25px
}

.plywood-since{
    color: var(--black, #262626);
    font-family: 'Skywalks';
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.5px;
}

.plywood-year{
 text-align: center;
font-family: Didone Room Numbers;
font-size: 100px;
font-style: normal;
font-weight: 400;
line-height: 100%; /* 200px */
letter-spacing: 10px;
   background: url(../assets/bg.jpeg) 0 0 / cover no-repeat;
   color: black;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
   width: 100%;
background-size: cover;
background-position: center;
}

@media screen and (min-width : 768px) {
    .plywood-Component6-div{
        height: 67vh;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }
    .plywood-Component6-heading-img{
        width: 100%;
        background-repeat: no-repeat;
        max-width: 340px;
    }
    .plywood-since{
        font-size: 70px;
    }
    .plywood-Component6-heading-content{
        position: relative;
        left: 23px;
    }
}